import { ISegment } from "../interface";

const timeFlight = (segment: ISegment) => Math.trunc(Math.trunc(segment.duration / 60) / 24) > 0
    ? Math.trunc(Math.trunc(segment.duration / 60) / 24) + ' ' + Math.trunc(segment.duration / 60) % 24 + ' ' + segment.duration % 60 + ''
    : Math.trunc(segment.duration / 60) % 24 + ' ' + segment.duration % 60 + '';

export default timeFlight;